import React,{useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Badge from 'react-bootstrap/Badge';
import crossIcon from './CrossIcon.svg';
import '../style/custom.css';
import crossIconHover from './CrossIconHover.svg';


const ElementBadge = (props)=>{

  const [cross,setCross] = useState(crossIcon)



  return(

    <Badge pill
     variant = 'light'
     style = {{
       margin : 5,
     }}
      draggable = {'true'}
      onDragStart = {(e)=>{props.onDragStart(e,props.index)}}
      onDragEnter = {(e)=>{props.onDragEnter(e,props.index)}}
      onDragEnd = {()=>{props.onDragEnd()}}
     >

        <span className = {'unselectableText'} style = {{
          marginRight : 5
        }}>
          {props.item}
        </span>
        <span
        data-key = {props.index}
        onClick = {(event)=>{props.rmElement(event.target.dataset.key)}}

        >
          <img
            src = {cross}
            data-key = {props.index}
            style = {{fill : 'red'}}
            onMouseEnter = {()=>{setCross(crossIconHover)}}
            onMouseLeave = {()=>{setCross(crossIcon)}}
            />
        </span>
    </Badge>


  );

}

export default ElementBadge;
