import React,{useState} from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import InputGroup from 'react-bootstrap/InputGroup';
import FormControl from 'react-bootstrap/FormControl';
import Navbar from 'react-bootstrap/Navbar';
import Button from 'react-bootstrap/Button';
import HelpTooltip from './components/HelpTooltip';
import ElementsField from './components/ElementsField';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';


const InputElemet = ({fieldName,array,setArray,tips,required})=>{

  const [value,setValue] = useState();
  const handleChange = (event)=>{
    setValue(event.target.value);
  }

  const addValue = ()=>{
    setArray([...array,value]);
    setValue('');
  }

  const handleEnterPress = (event)=>{
    if(event.key == 'Enter'){
      addValue();
    }
  }

  return(
    <InputGroup size="md" className="mb-3" onChange = {handleChange}>
      <InputGroup.Prepend>
        <InputGroup.Text  id="inputGroup-sizing-sm" style = {{backgroundColor : 'black',color : '#dee4ed',}}>
            {fieldName}
            {required ? <small style ={{color : 'red',fontSize : 16}}>*</small> : null}
        </InputGroup.Text>
      </InputGroup.Prepend>
      <FormControl onKeyPress = {handleEnterPress} value = {value} aria-label="Small" aria-describedby="inputGroup-sizing-sm" style = {{backgroundColor : '#040404',opacity : 0.7,color : 'white'}} />
      <InputGroup.Append>
        <Button
          style = {{padding : 0,backgroundColor : '#040404', borderColor : '#ced4da', borderLeft : 'none',opacity : 0.7,}}><HelpTooltip tips = {tips} /></Button>
      </InputGroup.Append>
    </InputGroup>
  );
}

export default InputElemet;
