import React,{useState,useRef} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Badge from 'react-bootstrap/Badge';
import ElementBadge from './ElementBadge';


const ElementsField = ({array,setArray})=>{

  const removeElement = (index)=>{
    if(index > -1){
        let arrayMod = Object.assign([],array);
        arrayMod.splice(index,1);
        setArray(arrayMod);


      }
  }

  const draggingItem = useRef();
  const draggingOverItem = useRef();

  const handleDragStart = (e, position)=>{
    draggingItem.current = position;
    //console.log(e.target.innerHTML);
  }

  const handleDragEnter = (e, position)=>{
    draggingOverItem.current = position;
  //  console.log(e.target.innerHTML);
  }

  const handleDragEnd = ()=>{
  
    const arrayCopy = Object.assign([],array);
    const draggingItemContent = arrayCopy[draggingItem.current];
    arrayCopy.splice(draggingItem.current,1);
    arrayCopy.splice(draggingOverItem.current,0,draggingItemContent);
    draggingItem.current = null;
    draggingOverItem.current = null;
    setArray(arrayCopy);
  }

  const handleElements = (item,index)=>{
    return <ElementBadge
              item = {item}
              index = {index}
              rmElement = {removeElement}
              onDragStart = {handleDragStart}
              onDragEnter = {handleDragEnter}
              onDragEnd = {handleDragEnd}
             />
  }


  return(
    <div
    style = {{padding : 5,
              background : ' background:#7f7f7f',
              background: 'rgba(255,255,255,0.1)',
              marginBottom : 20,
              borderRadius :5
            }}>


      { array.map(handleElements) }




    </div>


  );


}

export default ElementsField ;
