import React,{useState} from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';
import './style/custom.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import InputGroup from 'react-bootstrap/InputGroup';
import FormControl from 'react-bootstrap/FormControl';
import Navbar from 'react-bootstrap/Navbar';
import Button from 'react-bootstrap/Button';
import SpiderIcon from './components/SpiderIcon';
import PlusIcon from './components/PlusIcon';
import ElementsField from './components/ElementsField';
import Tooltip from 'react-bootstrap/Tooltip';
import InputElement from './InputElement';

const Form = ()=>{



  const [selectedFile,setSelectedFile] = useState(null);

  const handleFile = (event) =>{
    setSelectedFile(event.target.files[0]);
    console.log(event.target.files[0]);
  }

  const tips = {
    xpath : 'Xpath bla bla bla bla bla..',
    xpath_errors : 'Xpath errors bla bla bla bla bla..',
    column_names : 'Cloumn names ath bla bla bla bla bla..bla bla bla bla bla..',
    starting_url : 'Starting URL ath bla bla bla bla bla..bla bla bla bla bla..',
    url_queries : 'URL queries ........',
    product_xpath : 'Lorem ipsum bla bla bla',
    regex : 'Regex tiptool....',
    page_queries : 'Page queriesss..........',
    page_steps : 'Page steps.......................',
    search_xpath : 'Search Xpath.......',
    search_terms : 'Search terms......'
  };

  const [urlArray,setUrlArray]=useState([]);
  const [xpathArray,setXpathArray]=useState([]);
  const [columNamesArray,setColumnNamesArray]=useState([]);
  const [errorPathArray,setErrorPathArray]=useState([]);
  const [urlQueriesArray,setUrlQueriesArray]=useState([]);
  const [productXpathArray,setProductXpathArray]=useState([]);
  const [regexArray,setRegexArray]=useState([]);
  const [pageQueriesArray,setPageQueriesArray]=useState([]);
  const [pageStepsArray,setPageStepsArray]=useState([]);
  const [searchXpathArray,setSearchXpathArray]=useState([]);
  const [searchTermsArray,setSearchTermsArray]=useState([]);

  const [lastPageXpath,setLastPageXpath]=useState([]);
  const [predifinedUrlList,setPredifinidedUrlList]=useState([]);
  const [loadMore,setLoadMore]=useState([]);
  const [infiniteScroll,setInfiniteScroll]=useState([]);


  const handleSubmit = ()=>{
    const body = new FormData();
    body.append('starting_url', urlArray);
    body.append('column_name',columNamesArray);
    body.append('xpaths',xpathArray);
    body.append('error_xpaths',errorPathArray);
    body.append('url_queries',urlQueriesArray);
    body.append('product_xpath',productXpathArray);
    body.append('regex',regexArray);
    body.append('page_query',pageQueriesArray);
    body.append('page_steps',pageStepsArray);
    body.append('search_xpath',searchXpathArray);
    body.append('search_terms',searchTermsArray)


    /*const body = {
      'starting_url' : urlArray,
      'xpaths' : xpathArray,
      'column_names' : columNamesArray,
      'error_xpaths' :errorPathArray,
    }*/

    const endPoint = 'http://localhost:7555/api/create/';
    fetch(endPoint,{
      method: 'POST',
      body : body,
      mode : 'cors',
  })
      .then((response) => {

        return response.json();

      })

      .catch((err)=>{
        if(err){
        console.log(err.message)}});

    //  console.log(file);
    //  console.log(selectedFile);
  }




  return(
    <Container fluid
      className = {'h-100'}
      style = {{
      background : 'rgb(28,28,28)',
      background : 'linear-gradient(90deg, rgba(28,28,28,1) 8%, rgba(38,80,94,1) 65%, rgba(63,146,164,1) 91%)',
      minHeight : '100vh'
      }}
      >
    <Navbar className = {'fixed-top'} style = {{color : '#red'}}>
        <Navbar.Brand href="/" style = {{ color : 'white'}}>

        <SpiderIcon />

        Venom Crawler</Navbar.Brand>
    </Navbar>
      <Row className="justify-content-md-center h-100" >
        <Col xs lg="10" style = {{marginTop : 50}}>


        <Row style = {{marginTop : 20}}>
          <Col xs lg='6'>
            <InputElement required fieldName = {'xPATH'} array = {xpathArray} setArray = {setXpathArray} tips = {tips.xpath}/>
            { xpathArray.length > 0 ? <ElementsField array = {xpathArray} setArray = {setXpathArray}/> : null}

            <InputElement fieldName = {'Errors Xpath:'} array = {errorPathArray} setArray = {setErrorPathArray} tips = {tips.xpath_errors}/>
            { errorPathArray.length > 0 ? <ElementsField array = {errorPathArray} setArray = {setErrorPathArray}/> : null}

            <InputElement required fieldName = {'Column Names'} array = {columNamesArray} setArray = {setColumnNamesArray} tips = {tips.column_names}/>
            { columNamesArray.length > 0 ? <ElementsField array = {columNamesArray} setArray = {setColumnNamesArray}/> : null}

            <InputElement required fieldName = {'Starting URL'} array = {urlArray} setArray = {setUrlArray} tips = {tips.starting_url}/>
            { urlArray.length > 0 ? <ElementsField array = {urlArray} setArray = {setUrlArray}/> : null}

            <InputElement fieldName = {'URL queries'} array = {urlQueriesArray} setArray = {setUrlQueriesArray} tips = {tips.url_queries}/>
            { urlQueriesArray.length > 0 ? <ElementsField array = {urlQueriesArray} setArray = {setUrlQueriesArray}/> : null}


            <InputElement fieldName = {'Product Xpath:'} array = {productXpathArray} setArray = {setProductXpathArray} tips = {tips.product_xpath}/>
            { productXpathArray.length > 0 ? <ElementsField array = {productXpathArray} setArray = {setProductXpathArray}/> : null}

            <InputElement fieldName = {'Regex'} array = {regexArray} setArray = {setRegexArray} tips = {tips.regex}/>
            { regexArray.length > 0 ? <ElementsField array = {regexArray} setArray = {setRegexArray}/> : null}

            <InputElement fieldName = {'Page Queries'} array = {pageQueriesArray} setArray = {setPageQueriesArray} tips = {tips.page_queries}/>
            { pageQueriesArray.length > 0 ? <ElementsField array = {pageQueriesArray} setArray = {setPageQueriesArray}/> : null}

          </Col>


          <Col xs lg='6'>

            <InputElement fieldName = {'Last Page Xpath'} array = {lastPageXpath} setArray = {setLastPageXpath} tips = {tips.page_steps}/>
            { lastPageXpath.length > 0 ? <ElementsField array = {lastPageXpath} setArray = {setLastPageXpath}/> : null}

            <InputElement fieldName = {'Predifined UTL List'} array = {predifinedUrlList} setArray = {setPredifinidedUrlList} tips = {tips.page_steps}/>
            { predifinedUrlList.length > 0 ? <ElementsField array = {predifinedUrlList} setArray = {setPredifinidedUrlList}/> : null}

            <InputElement fieldName = {'Load More'} array = {loadMore} setArray = {setLoadMore} tips = {tips.page_steps}/>
            { loadMore.length > 0 ? <ElementsField array = {loadMore} setArray = {setLoadMore}/> : null}

            <InputElement fieldName = {'Infinite Scroll'} array = {infiniteScroll} setArray = {setInfiniteScroll} tips = {tips.page_steps}/>
            { infiniteScroll.length > 0 ? <ElementsField array = {infiniteScroll} setArray = {setInfiniteScroll}/> : null}

            <InputElement fieldName = {'Page Steps'} array = {pageStepsArray} setArray = {setPageStepsArray} tips = {tips.page_steps}/>
            { pageStepsArray.length > 0 ? <ElementsField array = {pageStepsArray} setArray = {setPageStepsArray}/> : null}


            <InputElement fieldName = {'Search Xpath'} array = {searchXpathArray} setArray = {setSearchXpathArray} tips = {tips.search_xpath}/>
            { searchXpathArray.length > 0 ? <ElementsField array = {searchXpathArray} setArray = {setSearchXpathArray}/> : null}


            <InputElement fieldName = {'Search Terms'} array = {searchTermsArray} setArray = {setSearchTermsArray} tips = {tips.search_terms}/>
            { searchTermsArray.length > 0 ? <ElementsField array = {searchTermsArray} setArray = {setSearchTermsArray}/> : null}

          </Col>
        </Row>


          <Row className = {"d-flex mb-3"}>
          <div class="p-2 mr-auto">
              <input  type = {'file'} name = {'upload_file'} id= {'upload_file'} style = {{display : 'none'}} onChange = {handleFile}/>
              <label for = {'upload_file'} className = {'btn btn-outline-light btn-md'}>
                Upload CSV
              </label>
              {selectedFile ? <p style = {{color : '#dee4ed',
                                          fontSize : 16,
                                          fontWeight : 800,
                                          letterSpacing : 5
                                        }}>{selectedFile.name}</p> : null}
          </div>

          <div className={"p-2"}>
              <Button as="input" variant="outline-light" type="button" value="Submit" size = 'md' onClick = {handleSubmit}/>
          </div>


          </Row>

        </Col>
        <Col xs lg="6">
        </Col>
      </Row>
    </Container>

  );
}


export default Form;
